(function () {
  'use strict';

  app.changeTimeZone = function () {
    $('.js-to-client-timezone').each(function (i, el) {
      var $el = $(el);
      var format = $el.data('format');
      var zone = moment.tz.guess();
      var time = $el.data('timezone');

      $el.html(moment.tz(time, zone).format(format));
    });

    $(document).trigger('app:change:timezone');
  };

  // TODO: FIXME: Essa chamada  para funcionar no primeiro load. No deveria
  // ser assim, parece ser algum erro com a insero de cod no tema
  app.changeTimeZone();
})();
